.mobile-menu-toggle {
  font-size: 20px;
  font-weight: 700;
  display: inline-flex;
  align-items: center;
  gap: 10px;
  margin-left: 25px;
  cursor: pointer;
  font-family: tablet-gothic;
}
@media (max-width: 767px) {
  .mobile-menu-toggle {
    font-size: 18px;
  }
}
.mobile-menu-toggle button, .mobile-menu-toggle .btn {
  border: 0;
  margin: -4px 0 0 0;
  padding: 0;
  background: none;
  position: relative;
  display: block;
  height: 25px;
  width: 20px;
}
@media (max-width: 767px) {
  .mobile-menu-toggle button, .mobile-menu-toggle .btn {
    padding-top: 0;
  }
}
.mobile-menu-toggle button span, .mobile-menu-toggle .btn span {
  display: block;
  height: 2px;
  margin: 0px 0;
  width: 20px;
  background: #011F5B;
  position: absolute;
  left: 0;
  transform: rotate();
  transition: all ease-in-out 0.3s;
}
.mobile-menu-toggle button span:nth-child(1), .mobile-menu-toggle .btn span:nth-child(1) {
  top: 5px;
}
.mobile-menu-toggle button span:nth-child(2), .mobile-menu-toggle .btn span:nth-child(2) {
  top: 12px;
}
.mobile-menu-toggle button span:nth-child(3), .mobile-menu-toggle .btn span:nth-child(3) {
  top: 19px;
}
.mobile-menu-toggle.active button span:nth-child(1), .mobile-menu-toggle.active .btn span:nth-child(1) {
  transform: rotate(45deg);
  top: 10px;
}
.mobile-menu-toggle.active button span:nth-child(2), .mobile-menu-toggle.active .btn span:nth-child(2) {
  opacity: 0;
}
.mobile-menu-toggle.active button span:nth-child(3), .mobile-menu-toggle.active .btn span:nth-child(3) {
  transform: rotate(-45deg);
  top: 10px;
}

